export default function Page() {
  return (
    <div className="practice-html">
      <style>{`
        body {
          background-color: #f0f0f0;
        }
        h1 {
          background-image: url("/practice/images/780.jpg");
          background-position: 50% 50%;
          background-repeat: no-repeat;
          text-align: center;
        }
        .box {
          width: 200px;
          height: 200px;
          border: dashed red 1px;
          padding: 20px;
          margin-bottom: 2em;
          border-radius: 20px;
        }
        .display-demo p {
          display: inline;
        }
        .flex-container {
          display: flex;
          flex-direction: row;
          justify-content: center;
        }
        .flex-container p {
          border: 1px solid red;
          margin-right: 1em;
          padding: 10px;
        }
        .animate-box {
          width: 100px;
          height: 100px;
          background-color: rgb(192, 184, 184);
          text-align: center;
          line-height: 100px;
          margin-top: 2em;
          border-radius: 0;
        }
        .animate-box:hover {
          border-radius: 100px;
          color: white;
          background-color: rgb(190, 21, 21);
          transition: all 0.3s;
        }
      `}</style>
      <h1>CSS 核心概念：背景、盒模型、布局、动画</h1>
      <section>
        <h2>背景</h2>
        <div>
          <p>
            &quot;cascading&quot; 的音标是 /kæˈskeɪdɪŋ/。如果你想在 HTML 文件里为 linear：
            英 [ˈlɪniə(r)]美 [ˈlɪniər]
          </p>
        </div>
      </section>
      <section>
        <h2>盒模型</h2>
        <div>
          <div className="box">box 中的内容 1</div>
          <div className="box">box 中的内容 2</div>
        </div>
      </section>
      <section>
        <h2>布局</h2>
        <div>
          <h3>dispaly 的不同取值</h3>
          <div className="display-demo">
            <p>壹1</p>
            <p>贰2</p>
            <p>叁3</p>
            <p style={{ display: 'none' }}>肆4</p>
          </div>
          <div className="flex-container">
            <p>111111111111111111111</p>
            <p>222222222222222</p>
            <p>3</p>
            <p>4</p>
          </div>
        </div>
      </section>
      <section>
        <h2>动画</h2>
        动画必须要有起始状态、结束状态、时长
        <div className="animate-box">盒模型内容</div>
      </section>
    </div>
  );
} 